import React from 'react'
import './ProductDetails.scss';

import Quantity from './Quantity/Quantity';

const ProductDetails = (props) => {


    const socilLinks = [
        {
            href: '#',
            icon: 'http://user6.itsindev.com/litmuslab/images/f.png'
        },
        {
            href: '#',
            icon: 'http://user6.itsindev.com/litmuslab/images/g.png'
        },
        {
            href: '#',
            icon: 'http://user6.itsindev.com/litmuslab/images/t.png'
        },
        {
            href: '#',
            icon: 'http://user6.itsindev.com/litmuslab/images/li.png'
        },
    ]



    return (
        <div class="products_ditels_sec_right">
            <div class="form_sec">

                <h1>{props.itemName}</h1>
                <h3>{props.price}</h3>

                <Quantity onBuy={props.onBuy} />

                <ul class="socil_icon">
                    <li><strong>Share this....</strong></li>

                    {
                        socilLinks.map((link, i) => {
                            return (
                                <li key={i}>
                                    <a href={link.href}>
                                        <img src={link.icon} alt="" />
                                    </a>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </div>
    )
}

export default ProductDetails
