import React from 'react'
import './Quantity.scss';

class Quantity extends React.Component {

    state = {
        qtyInput: 1
    }

    onInputChange = (e) => {
        const target = e.target;
        const val = target.value;
        this.setState({ qtyInput: val * 1 })
    }

    onUpdateQty = (action) => {

        let qty = this.state.qtyInput;

        if (action === 'add') {
            qty = qty + 1;
        } else {
            if (qty > 1) {
                qty = qty - 1;
            }
        }

        this.setState({ qtyInput: qty * 1 })
    }

    render() {
        return (
            <ul class="quantity_sec">
                <li> <strong>Quantity</strong></li>
                <li>
                    <input class="minus" type="button" value="-"
                        onClick={
                            () => { this.onUpdateQty('remove') }
                        }
                    />
                </li>
                <li>
                    <input
                        className="input-text qty text"
                        type="number"
                        step="1"
                        min="1"
                        max=""
                        name="quantity"
                        value={this.state.qtyInput}
                        title="Qty"
                        size="4"
                        pattern="[0-9]*"
                        inputmode="numeric"
                        onChange={this.onInputChange}
                    />
                </li>
                <li>
                    <input class="plus" type="button" value="+"
                        onClick={
                            () => { this.onUpdateQty('add') }
                        }
                    />
                </li>
                <li>
                    <button type="button" onClick={() => {
                        this.props.onBuy(this.state.qtyInput)
                    }}>Buy it now</button>
                </li>

            </ul>
        )
    }
}

export default Quantity
