import React, { Component } from 'react';


export default class Video extends Component {
    render() {
        return (

            <div className="row">
                <div className="col">
                    <video width="100%" height="500" controls>
                        <source src="http://user6.itsindev.com/litmuslab/images/demo.mp4" type="video/mp4" />
                        <source src="http://user6.itsindev.com/litmuslab/images/demo.mp4" type="video/ogg" />
                    </video>
                </div>
            </div>

        )
    }

}
