import React, { Component } from 'react'

import './Gallery.scss';

import banner from '../../images/banner.png';
import banner1 from '../../images/banner1.png';
import banner2 from '../../images/banner2.png';
import banner3 from '../../images/banner3.png';

export default class Gallery extends Component {

    state = {
        images: [
            { active: true, imgUrl: banner },
            { active: false, imgUrl: banner1 },
            { active: false, imgUrl: banner2 },
            { active: false, imgUrl: banner3 }
        ]
    }


    onChangeSlide = (index) => {

        const updatedImages = this.state.images.map((item, i) => {

            if (i === index) {
                item.active = true;
            } else {
                item.active = false;
            }

            return item;
        });

        this.setState({ images: updatedImages })
    }

    render() {
        return (
            <div className="row">
                <div className="col-12  ">
                    <div className="gallery">
                        <div className="row">
                            <div className="col-12">
                                <div className="showImage">
                                    {
                                        this.state.images.map(img => {
                                            let imgTag = null;
                                            if (img.active) {
                                                imgTag = (<img src={img.imgUrl} alt="" />);
                                            }
                                            return imgTag;
                                        })
                                    }
                                </div>
                            </div>
                            <div className="col-md-12">
                                <ul className="tumbnails">

                                    {
                                        this.state.images.map((item, i) => {
                                            return (
                                                <li
                                                    key={'img_' + i}
                                                    className={item.active ? 'active' : null}
                                                    onClick={() => {
                                                        this.onChangeSlide(i)
                                                    }}
                                                >
                                                    <img src={item.imgUrl} class="img-responsive"
                                                        alt="tortoise" data-desoslide-caption-title="A tortoise" />

                                                </li>
                                            )
                                        })
                                    }

                                </ul>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}
